body {
  width: 100vw;
  max-width: 100vw;
  overflow-x: hidden !important;
  background-color: #9d9d9d;
  .mobile-menu {
    position: fixed;
    top: 0;
    bottom: 0;
    width: 100%;
    right: -100%;
    transition: 0.3s;
    background-color: $color-green;
    color: $color-white;
    text-align: right;
    padding-right: 20px;
    padding-top: 50px;
    z-index: 200;
    ul {
      list-style-type: none;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      color: $color-white;
      height: 100%;
      margin-top: -100px;
      li {
        margin-top: 25px;
        display: block;
        font-family: $font-metroflex;
        font-size: 32px;
        a {
          color: $color-white;
        }
      }
    }
    .fa-times {
      font-size: 30px;
    }
  }
  .mobile-menu__expand {
    right: 0;
    z-index: 200;
  }
  .container {
    max-width: 1160px !important;
    position: relative;
    .top-nav {
      z-index: 100;
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      margin: auto;
      max-width: 1160px;
      padding-left: 15px;
      padding-right: 15px;
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      padding-top: 50px;
      @media (max-width: 767px) {
        padding-top: 50px;
      }
      &__logo {
        width: 191px;
        height: 34px;
        a {
          img {
            width: 100%;
          }
        }
      }
      &__menu {
        @media (max-width: 767px) {
          display: none;
        }
        ul {
          display: flex;
          flex-direction: row;
          justify-content: flex-end;
          list-style: none;
          @media (max-width: 767px) {
            display: none;
          }
          li {
            padding-right: 45px;
            padding-left: 45px;
            border-right: 2px solid $color-white;
            height: 15px;
            text-align: center;
            @media (max-width: 565px) {
              text-align: right;
              border-right: none;
              margin-bottom: 25px;
            }
            &:last-child {
              border-right: none;
              padding-right: 30px;
              @media (max-width: 565px) {
                padding-right: 45px;
              }
            }
            a {
              color: $color-white;
              font-family: $font-metroflex;
              font-size: 18px;
              letter-spacing: 0.08px;
              margin-top: -6px;
              display: block;
            }
          }
        }
      }
      .hamburger {
        display: none;
        font-size: 30px;
        color: $color-white;
        @media (max-width: 767px) {
          display: block;
        }
      }
    }
  }
  .top-slider {
    background-color: #2e4940;
    margin-bottom: 0;
    max-height: 100vh;
    @media (max-width: 1200px) {
      max-height: 75vh;
    }
    @media (max-width: 991px) {
      max-height: 655px;
    }
    @media (max-width: 767px) {
      max-height: 555px;
    }
    @media (max-width: 575px) {
      max-height: 430px;
    }
    @media (max-width: 515px) {
      max-height: 400px;
    }
    @media (max-width: 450px) {
      max-height: 350px;
    }
    &__item {
      height: 100%;
      background-position: top;
      border-bottom-left-radius: 320px 150px;
      border-bottom-right-radius: 320px 150px;
      box-shadow: 0 -70px 30px -30px rgba(0, 0, 0, 0.3) inset;
      background-size: cover !important;
      overflow: hidden;
      @media (max-width: 1680px) {
        border-bottom-left-radius: 250px 113px;
        border-bottom-right-radius: 250px 113px;
      }
      @media (max-width: 1600px) {
        border-bottom-left-radius: 150px 68px;
        border-bottom-right-radius: 150px 68px;
      }
      @media (max-width: 1400px) {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
      }
      @media (max-width: 1200px) {
        background-size: cover;
        max-height: 75vh;
      }
      @media (max-width: 991px) {
        max-height: 655px;
      }
      @media (max-width: 767px) {
        max-height: 555px;
      }
      @media (max-width: 575px) {
        max-height: 430px;
      }
      @media (max-width: 515px) {
        max-height: 400px;
      }
      @media (max-width: 450px) {
        max-height: 350px;
      }
      h2 {
        color: $color-white;
      }
    }
    .top-slick-next {
      top: 50vh;
      position: absolute;
      right: calc(50vw - 580px + 21px);
      font-family: $font-metroflex;
      font-size: 66px;
      &:hover {
        cursor: pointer;
      }
      @media (max-width: 1200px) {
        top: 37vh;
        right: 50px;
      }
      @media (max-width: 991px) {
        top: 300px;
      }
      @media (max-width: 767px) {
        display: none;
        visibility: hidden;
        top: 700px;
      }
      &:before {
        content: ">";
        color: $color-white;
        background-image: url("../../images/arrow-right.svg");
      }
    }
    .top-slick-prev {
      top: 50vh;
      position: absolute;
      left: calc(50vw - 580px + 21px);
      z-index: 10;
      font-family: $font-metroflex;
      font-size: 66px;
      &:hover {
        cursor: pointer;
      }
      @media (max-width: 1200px) {
        top: 37vh;
        left: 50px;
      }
      @media (max-width: 991px) {
        top: 300px;
      }
      @media (max-width: 767px) {
        display: none;
      }
      &:before {
        content: "<";
        color: $color-white;
        background-image: url("../../images/arrow-left.svg");
      }
    }
    ul.slick-dots {
      bottom: 100px;
      @media (max-width: 991px) {
        bottom: 60px;
      }
      @media (max-width: 575px) {
        bottom: 45px;
      }
      li[role=presentation] {
        color: $color-white;
        border: 2px solid $color-white !important;
        border-radius: 50%;
        button {
          font-size: 0;
          display: block;
          width: 12px;
          height: 12px;
          &:before {
            font-size: 0;
            border: 2px solid $color-white;
            border-radius: 50%;
            color: transparent;
            opacity: 0;
          }
        }
      }
      li.slick-active {
        border: 2px solid $color-white;
        border-radius: 50%;
        top: 2px;
        button {
          font-size: 0;
          display: block;
          width: 12px;
          height: 12px;
          margin-top: 2px;
          padding-right: 6px;
          padding-left: 6px;
          margin-left: 2px;
          cursor: pointer;
          border-radius: 50%;
          background-color: white;
          &:before {
            font-size: 0;
          }
        }
      }
    }
  }
  .content {
    background-image: radial-gradient(circle 1094px at 50% 50%, #548473 0%, #2e4940 80%);
    box-shadow: 0 -110px 30px -80px rgba(0, 0, 0, 0.3) inset;
    margin-top: -40px;
    display: block;
    border-bottom-left-radius: 320px 145px;
    border-bottom-right-radius: 320px 145px;
    z-index: 20;
    position: relative;
    @media (max-width: 1680px) {
      border-bottom-left-radius: 250px 113px;
      border-bottom-right-radius: 250px 113px;
    }
    @media (max-width: 1600px) {
      border-bottom-left-radius: 150px 68px;
      border-bottom-right-radius: 150px 68px;
    }
    @media (max-width: 1400px) {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
    .pack-desc {
      .container {
        display: flex;
        justify-content: center;
        flex-direction: row;
        @media (max-width: 991px) {
          flex-direction: column;
          justify-content: center;
          align-items: center;
        }
        h2 {
          text-align: right;
          width: 50%;
          display: block;
          color: $color-white;
          font-family: $font-metroflex-bold;
          font-size: 49px;
          line-height: 56px;
          letter-spacing: -1.66px;
          margin-top: 50px;
          @media (max-width: 991px) {
            width: 100%;
            text-align: center;
            font-size: 40px;
            line-height: 46px;
            margin-top: 40px;
          }
          @media (max-width: 767px) {
            font-size: 32px;
            line-height: 36px;
          }
        }
        .description-image {
          margin-top: -50px;
          z-index: 100;
          width: 50%;
          margin-left: 17px;
          @media (max-width: 991px) {
            margin-top: 30px;
            width: 100%;
            max-width: 556px;
            padding-top: 40px;
            padding-bottom: 40px;
          }
          img {
            width: 100%;
            max-width: 556px;
          }
          &__footnote {
            position: absolute;
            margin-left: 450px;
            margin-top: -120px;
            width: 130px;
            color: $color-white;
            font-family: $font-metroflex;
            font-size: 17px;
            padding-right: 3px;
            line-height: 20px;
            letter-spacing: -0.4px;
            @media (max-width: 1200px) {
              position: relative;
              font-size: 17px;
              margin-left: 0;
              margin-top: 0;
              width: 100%;
            }
            @media (max-width: 991px) {
              text-align: right;
              font-size: 15px;
            }
          }
        }
      }
    }
    .search-block {
      color: $color-white;
      margin-top: 140px;
      @media (max-width: 991px) {
        margin-top: 60px;
      }
      @media (max-width: 565px) {
        margin-top: 25px;
      }
      .container {
        h2 {
          text-align: center;
          color: $color-white;
          font-family: $font-metroflex-bold;
          font-size: 65px;
          @media (max-width: 991px) {
            font-size: 55px;
          }
          @media (max-width: 767px) {
            font-size: 32px;
            line-height: 36px;
          }
        }
        .search-block__form {
          width: 100%;
          max-width: 993px;
          position: relative;
          right: 0;
          left: 0;
          margin: auto;
          margin-top: 50px;
          display: flex;
          padding-left: 15px;
          input {
            border-radius: 25px;
          }
          label {
            z-index: 100;
            background-color: $color-white;
            width: 80px;
            height: 80px;
            border-radius: 50%;
            padding-top: 20px;
            display: flex;
            justify-content: center;
            align-items: center;
            padding-bottom: 20px;
            margin-left: -60px;
            margin-top: -20px;
            &:hover {
              cursor: pointer;
            }
          }
        }
      }
    }
  }
  .video {
    background-image: radial-gradient(circle 1094px at 50% 50%, #fff 0%, #9d9d9d 80%);
    padding-bottom: 110px;
    z-index: 60;
    position: relative;
    @media (max-width: 991px) {
      padding-bottom: 50px;
    }
    @media (max-width: 575px) {
      padding-bottom: 50px;
      margin-top: -40px;
    }
    &__container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      transition: 0.3s;
      .video__title {
        margin-top: 130px;
        text-align: center;
        color: $color-green;
        font-family: $font-metroflex-bold;
        font-size: 80px;
        @media (max-width: 991px) {
          margin-top: 60px;
          font-size: 65px;
        }
        @media (max-width: 767px) {
          font-size: 32px;
          line-height: 36px;
        }
      }
      .video__subtitle {
        font-family: $font-metroflex;
        color: $color-green;
        text-align: center;
        font-size: 50px;
        letter-spacing: -0.09px;
        margin-bottom: 70px;
        @media (max-width: 991px) {
          margin-bottom: 40px;
          font-size: 55px;
        }
        @media (max-width: 767px) {
          margin-bottom: 35px;
          font-size: 26px;
          line-height: 36px;
        }
        @media (max-width: 565px) {
          margin-bottom: 25px;
          font-size: 30px;
        }
      }
      video {
        width: 1048px;
        height: 616px;
        @media (max-width: 1200px) {
          width: 100%;
          padding-right: 25px;
          padding-left: 25px;
          height: auto;
        }
        @media (max-width: 576px) {
          padding-right: 15px;
          padding-left: 15px;
        }
      }
    }
  }
}



