footer {
  background-color: $color-green;
  padding-top: 53px;
  padding-bottom: 45px;
  position: relative;
  z-index: 60;
}
.footer-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  @media (max-width: 991px) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .footer__left {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin-right: 15px;
    @media (max-width: 991px) {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin-right: 0;
    }
    p {
      color: $color-white;
      font-family: $font-metroflex-bold;
      font-size: 18px;
      @media (max-width: 991px) {
        text-align: center;
        margin-top: 20px;
      }
    }
    img {
      height: fit-content;
    }
  }
  .footer__right {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    margin-left: 15px;
    @media (max-width: 991px) {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin-left: 0;
    }
    p {
      text-align: right;
      color: $color-white;
      font-family: $font-metroflex-bold;
      font-size: 18px;
      @media (max-width: 991px) {
        margin-top: 20px;
        text-align: center;
      }
    }
    .footer-icons {
      color: $color-white;
      font-size: 18px;
      margin-top: 20px;
      a {
        color: $color-white;
        i {
          margin-left: 30px;
        }
        &:first-child {
          i {
            margin-left: 0;
          }
        }
      }
    }
  }
}



