.tabs {
  margin-top: 45px;
  position: relative;
  z-index: 50;
  overflow-y: hidden;
  @media (max-width: 565px) {
    margin-top: 30px;
  }
  .tab-content {
    //margin-top: 90px;
    @media (max-width: 565px) {
      margin-top: 40px;
    }
    .tab-item {
      padding: 65px 80px 65px 80px;
      box-shadow: 0 -70px 30px -30px rgba(0, 0, 0, 0.3) inset;
      background-color: $color-white;
      color: $color-green;
      border-top-left-radius: 25px;
      border-top-right-radius: 25px;
      min-height: 500px;
      position: absolute;
      display: block;
      bottom: -200%;
      transition: 0.3s;
      @media (max-width: 767px) {
        padding: 50px 60px 50px 60px;
      }
      @media (max-width: 565px) {
        padding: 25px 30px 25px 30px;
      }
      &__heading {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        h2 {
          color: $color-green;
          font-family: $font-metroflex-bold;
          font-size: 35px;
          text-transform: uppercase;
          position: relative;
          width: 100%;
          @media (max-width: 565px) {
            font-size: 28px;
          }
        }
        span {
          width: 15px;
          height: 15px;
          display: block;
          background-image: url("../../images/cross.svg");
          position: relative;
          text-align: right;
          right: -60px;
          top: -45px;
          background-repeat: no-repeat;
          @media (max-width: 767px) {
            right: 0;
            top: 0;
          }
          &:hover {
            cursor: pointer;
          }
        }
      }
      p {
        color: $color-green;
        font-family: $font-metroflex;
        font-size: 18px;
        line-height: 28px;
        @media (max-width: 565px) {
          font-size: 16px;
          line-height: 24px;
        }
      }
    }
    .active-tab {
      display: block;
      position: absolute;
      bottom: 0;
      width: 100%;
      @media (max-width: 767px) {
        margin-bottom: 40px;
      }
    }
  }
  .tabs__faq {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    margin-top: 90px;
    padding-bottom: 150px;
    height: auto;
    opacity: 1;
    transition: 0.3s;
    @media (max-width: 767px) {
      flex-direction: column;
      align-items: center;
    }
    &__left {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      width: 49%;
      @media (max-width: 767px) {
        align-items: center;
        margin-bottom: 15px;
        width: 100%;
        padding-right: 15px;
        padding-left: 15px;
      }
    }
    &__right {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      width: 49%;
      @media (max-width: 767px) {
        align-items: center;
        width: 100%;
        padding-right: 15px;
        padding-left: 15px;
      }
      .tabs__interbar {
        margin-left: auto;
        width: 100%;
      }
    }
    &__divider {
      background-color: $color-white;
      display: block;
      width: 1px;
      @media (max-width: 991px) {
        background-color: transparent;
      }
    }
    .tabs__interbar {
      margin-top: 17px;
      max-width: 453px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      background-color: $color-white;
      border-radius: 10px;
      height: 58px;
      transition: 0.3s;
      @media (max-width: 767px) {
        width: 100%;
        max-width: 100%;
      }
      &:first-child {
        margin-top: 0;
      }
      &:hover {
        cursor: pointer;
      }
      .tabs__faq-icon {
        width: 80px;
        display: flex;
        justify-content: center;
        img {
          max-width: 70px;
        }
      }
      p {
        margin-bottom: 0;
        text-align: left;
        font-family: $font-metroflex-bold;
        color: $color-green;
        font-size: 14px;
        font-weight: 400;
        line-height: 14px;
        width: 100%;
      }
      .tabs__plus {
        padding-right: 25px;
      }
    }
  }
  .tabs__faq__disabled {
    opacity: 0;
    transition: 0.3s;
    position: relative;
  }
  &__container {
    margin: 0 auto;
    max-width: 780px;
    width: 780px;
    @media (max-width: 991px) {
      max-width: 100%;
      width: 100%;
    }
    .tab-labels {
      position: relative;
      height: 32px;
      @media (max-width: 767px) {
        margin-bottom: -40px;
      }
      input {
        display: none;
      }
      label {
        cursor: pointer;
        font-family: $font-metroflex;
        color: $color-green;
        font-size: 15px;
        max-width: 133px;
        width: 133px !important;
        height: 31px;
        padding-top: 4px;
        background-color: $color-white;
        text-align: center;
        border-radius: 15px;
      }
      .tab-slick-next {
        position: absolute;
        margin-top: -43px;
        margin-right: -40px;
        right: 0;
        font-family: $font-metroflex;
        font-size: 32px;
        &:hover {
          cursor: pointer;
        }
        @media (max-width: 991px) {
          margin-right: 0;
        }
        &:before {
          content: ">";
          color: $color-white;
          background-image: url("../../images/arrow-right.svg");
        }
      }
      .tab-slick-prev {
        position: absolute;
        margin-top: -11px;
        left: -40px;
        right: 0;
        font-family: $font-metroflex;
        font-size: 32px;
        z-index: 200;
        width: 10px;
        &:hover {
          cursor: pointer;
        }
        @media (max-width: 991px) {
          left: 0;
        }
        &:before {
          content: "<";
          color: $color-white;
          background-image: url("../../images/arrow-left.svg");
        }
      }
    }
  }
  .slick-slide {
    display: flex;
    justify-content: center;
  }
}



